<template>
    <div class="content-table">
        <el-card class="box-card" v-if="data_found">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
            </div>
            <el-row class="line-bottom">
            <el-col class="line-right" :span="10">
            <el-form class="form-application-add" label-position="left" label-width="150px">
                <el-form-item label="Application">
                    {{ name }}
                </el-form-item>
                <el-form-item label="Icon">
                    <el-image
                        style="width: 100px; height: 100px"
                        :src="icon"
                        fit="cover">
                    </el-image>
                </el-form-item>
                <el-form-item label="Play Store">
                    <el-button @click="playStore(this.package)" size="small" target="_blank" round>Play Store</el-button>
                </el-form-item>
                <el-form-item label="Key">
                    {{  key  }}
                </el-form-item>
                <el-form-item label="Limit">
                    {{ limit }}
                </el-form-item>
            </el-form>
            </el-col>
            <el-col :span="14">
                <el-card class="box-card" style="margin : 5px">
                    <div slot="header" class="clearfix">
                        <span>Carousel Main Android</span>
                        <el-button style="float: right; padding: 3px 0" type="text">Edit</el-button>
                    </div>
                    <el-carousel :interval="2000" type="card" height="400px">
                        <el-carousel-item v-for="item in 6" :key="item">
                        <h3 class="medium">{{ item }}</h3>
                        </el-carousel-item>
                    </el-carousel>
                </el-card>
            </el-col>
            </el-row>
            <el-row style="width:80%; margin : 0 auto; margin-top : 20px">
                <el-col :span="4" v-for="(o, index) in image" :key="index" style="margin : 5px" >
                    <el-card :body-style="{ padding: '0px' }">
                    <img :src="o.url" class="image">
                    <div style="margin: 0 auto;">
                        <el-button style="width : 100%" type="danger" icon="el-icon-delete" size="mini" @click="deleteData(o.id_images)">Delete</el-button>
                    </div>
                    </el-card>
                </el-col>
            </el-row>
        </el-card>
        <el-alert v-else
            title="application not found"
            type="error"
            @close="routeApplication">
        </el-alert>
    </div>
</template>

<script>

import axios from '../../../axios.js'
import router from '@/router'

export default {
    data () {
        return {
            data_found : false,
            name : '',
            icon : '',
            package : '',
            key : '',
            limit : '',
            image : []
        }
    },
    methods : {
        routeApplication () {
            router.push('/application')  
        },
        playStore (packagePlayStore) {
            const pl = packagePlayStore
            window.open('https://play.google.com/store/apps/details?id='+pl, '_blank'); 
        },
        deleteData(dataID) {
            // const id = dataID
            this.$alert('Are you sure you delete this image', 'Delete!', {
            confirmButtonText: 'OK',
            callback: action => {
                this.dataDel(dataID)
                this.$message({
                type: 'info',
                message: `action: ${ action }`
                });
            }
        });
      },
        dataDel(dataID) {
            const accessToken = {
                headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
            }
            axios.delete(`image/${dataID}/${this.$route.params.id}/delete`, accessToken)
                .then((response) => {
                    if (response.data.success == true) {
                        const id = parseInt(response.data.data)
                        const ItemIndex = this.image.findIndex((c) => c.id_images === id)
                        this.image.splice(ItemIndex, 1)
                }
            })
        }
    },
    computed : {
        baseUrl () {
            const path = this.$route.path
            return path[0]
        },
        headerCard () {
            return this.$route.name
        },
        detailData () {
            return this.data
        }
        
    },
    async created () {
        const accessToken = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        }
        await axios.get(`application/${this.$route.params.id}/${this.$store.state.userDataActive.id}/image/detail`, accessToken)
            .then((response) => {    
                if (response.data.success == true) {
                    this.data_found = true        
                    this.name = response.data.data.name_applications,
                    this.icon = response.data.data.url,
                    this.package = response.data.data.package_applications,
                    this.key = response.data.data.key_applications,
                    this.limit = response.data.data.limit_applications,
                    this.image = response.data.image
                } else {
                    this.data_found = false
                }
        })
    } 
}
</script>

<style>
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
.line-right {
    border-right: 1px solid #999;
    margin-bottom: 5px;
}
.line-bottom {
    border-bottom: 1px solid #999;
}
.form-application-add {
    margin-right: 25px;
    /* margin: 0 auto; */
    width: 500px;
}
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
.btn-input-add {
    margin: 0 auto;
    width: 250px;
}
.time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>